
import { PropType, defineComponent, computed } from 'vue';
import { useI18n } from 'vue-i18n';

import useNumbers from '@/composables/useNumbers';

import { DecoratedPool } from '@/services/balancer/subgraph/types';

import LiquidityAPRTooltip from '@/components/tooltips/LiquidityAPRTooltip.vue';

export default defineComponent({
  components: {
    LiquidityAPRTooltip
  },

  props: {
    pool: { type: Object as PropType<DecoratedPool> },
    loading: { type: Boolean, default: true }
  },

  setup(props) {
    // COMPOSABLES
    const { fNum } = useNumbers();
    const { t } = useI18n();

    // COMPUTED
    const stats = computed(() => {
      if (!props.pool) return [];

      return [
        {
          id: 'poolValue',
          label: t('poolValue'),
          value: fNum(props.pool.totalLiquidity, 'usd')
        },
        {
          id: 'volumeTime',
          label: t('volumeTime', ['24h']),
          value: fNum(props.pool.volume24h, 'usd')
        },
        {
          id: 'feesTime',
          label: t('feesTime', ['24h']),
          value: fNum(props.pool.fees24h, 'usd')
        },
        {
          id: 'apr',
          label: 'APR',
          value: fNum(props.pool.apr.total, 'percent')
        }
      ];
    });

    return {
      stats
    };
  }
});
